import { JSX, RefObject, Ref } from 'preact';
import { useLayoutEffect, MutableRef, useMemo } from 'preact/hooks';
import { forwardRef } from 'preact/compat';
import { useFloating, shift, flip, offset } from '../hooks/UNSAFE_useFloating';
import { autoUpdate } from '@floating-ui/dom';
import { Offset, Placement, Coords, VirtualElement, Rect } from '../hooks/UNSAFE_useFloating';
import useOutsideClick from '../hooks/UNSAFE_useOutsideClick';

type IntrinsicProps = Pick<JSX.HTMLAttributes<HTMLDivElement>, 'children' | 'class'>;

type Props = IntrinsicProps & {
  /**
   * Placement of the popup element relative to the trigger.
   */
  placement?: Placement;

  /**
   * Trigger element reference.
   */
  anchorRef: RefObject<HTMLElement | Coords>;

  /**
   * Placement offset.
   */
  offsetValue?: Offset;

  /**
   * Click outside callback.
   */
  onClickOutside?: (event: MouseEvent) => void;
};

function assignRef<T>(ref: Ref<T> | undefined, value: T | null) {
  if (ref == null) return;

  if (typeof ref === 'function') {
    ref(value);
    return;
  }

  try {
    // @ts-ignore
    ref.current = value;
  } catch (error) {
    throw new Error(`Cannot assign value '${value}' to ref '${ref}'`);
  }
}

function mergeRefs<T>(...refs: (Ref<T> | undefined)[]) {
  return (node: T | null) => {
    refs.forEach((ref) => assignRef(ref, node));
  };
}

export const Floating = forwardRef(
  (
    {
      children,
      onClickOutside,
      placement = 'bottom',
      anchorRef,
      offsetValue,
      class: className
    }: Props,
    ref: MutableRef<HTMLElement | null>
  ) => {
    const { x, y, reference, floating, refs } = useFloating({
      placement: placement,
      middleware: [offset(offsetValue), flip({ mainAxis: true, crossAxis: true }), shift()],
      whileElementsMounted: autoUpdate
    });

    if (onClickOutside) {
      useOutsideClick({ isDisabled: false, ref: refs.floating, handler: onClickOutside });
    }

    useLayoutEffect(() => {
      const coords = anchorRef as RefObject<Coords>;
      const element = anchorRef as RefObject<HTMLElement>;
      const virtualEl: VirtualElement = {
        getBoundingClientRect() {
          return {
            width: 0,
            height: 0,
            x: coords.current!.x,
            y: coords.current!.y,
            top: coords.current!.y,
            left: coords.current!.x,
            right: coords.current!.x,
            bottom: coords.current!.y
          };
        }
      };
      const result = coords.current && coords.current.x ? virtualEl : element.current;
      reference(result);
    }, [anchorRef.current]);

    const stableRef = useMemo(() => mergeRefs(ref, floating), [ref, floating]);

    const content = (
      <div
        class={className}
        ref={stableRef}
        style={{
          display: 'block',
          position: 'absolute',
          top: y ?? '',
          left: x ?? ''
        }}>
        {children}
      </div>
    );
    return content;
  }
);

export { Offset, Placement, Coords, Rect };
